@import url(https://fonts.googleapis.com/css?family=Cairo);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@-webkit-keyframes spin {
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes spin {
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

body {
  background-color: #292929;
}

#repeatButton {
  position: absolute;
  top: 10px;
  right: 20px;

  width: 48px;
  height: 48px;

  cursor: pointer;
  -webkit-animation: 6s linear infinite spin;
          animation: 6s linear infinite spin;

  border: none;
  background: url(https://andyhoffman.codes/random-assets/img/slots/repeat.png) transparent 0 0 no-repeat;
  background-size: cover;

  -webkit-appearance: none;

     -moz-appearance: none;

          appearance: none;
}

h1 {
  font-family: 'Cairo', sans-serif;
  font-size: 150%;

  padding: .25em .5em;
}

h1 span {
  padding: .1em .2em;

  color: aliceblue;
  border: 1px solid hsla(208, 100%, 97.1%, .2);
}

.icons {
  display: inline-block;
  overflow: hidden;

  width: 128px;
  height: 465px;
  padding: 0 80px;

  -webkit-transition: .3s background-position ease;

  transition: .3s background-position ease;
  -webkit-transform: translateZ(0);
          transform: translateZ(0);

  background: #fff url(/twin/static/media/css_sprites.b9fef22d.png) repeat-y;
  background-size: 155px;

  will-change: backgroundPosition;
}

.icons:nth-child(2n) {
  margin: 0 10px;
}

* {
  box-sizing: border-box;
}

.spinner-container {
  position: absolute;
  top: 50%;
  left: 50%;
  background-color: rgb(29, 103, 128);

  display: flex;
  overflow: hidden;

  padding: 2em;

  -webkit-transition: .3s transform;

  transition: .3s transform;
  -webkit-transform: translate(-50%, -50%) scale(.62, .62);
          transform: translate(-50%, -50%) scale(.62, .62);
}

.app-wrap.winner-false {
  -webkit-animation: linear 1s spin;
          animation: linear 1s spin;
}

.spinner-container::after {
  position: absolute;
  top: 50%;
  right: 30px;
  left: 30px;

  display: block;

  height: 155px;

  content: '';
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);

  background: rgba(46, 66, 68, 0.1);
}

.gradient-fade {
  position: absolute;
  top: 32px;
  right: 32px;
  bottom: 32px;
  left: 32px;

  background: -webkit-gradient(
  linear,
  left top, left bottom,
  from(rgba(64,64,64,1)),
  color-stop(7%, rgba(64,64,64,0)),
  color-stop(93%, rgba(64,64,64,0)),
  to(rgba(64,64,64,1))
  );

  background: linear-gradient(
  to bottom,
  rgba(64,64,64,1) 0%,
  rgba(64,64,64,0) 7%,
  rgba(64,64,64,0) 93%,
  rgba(64,64,64,1) 100%
  );
}

@media screen and (max-width: 1000px) {
  .spinner-container {
    -webkit-transform: translate(-50%, -50%) scale(.55, .55);
            transform: translate(-50%, -50%) scale(.55, .55);
  }
}

@media screen and (max-width: 500px) {
  .spinner-container {
    -webkit-transform: translate(-50%, -50%) scale(.4, .4);
            transform: translate(-50%, -50%) scale(.4, .4);
  }
}

